:root {
  --mdb-font-roboto: "Roboto", sans-serif;
  --mdb-bg-opacity: 1;
}

body {
  font-family: var(--mdb-font-roboto);
  line-height: 1.6;
  color: #525252;
}

a {
  text-decoration: none;
}

button:focus {
  outline: 0;
}

.note {
  padding: 10px;
  border-left: 6px solid;
  border-radius: 5px;
}
.note strong {
  font-weight: 600;
}
.note p {
  font-weight: 500;
}

.note-primary {
  background-color: rgb(237.8714285714, 242.7428571429, 253.4285714286);
  border-color: #2563eb;
}

.note-secondary {
  background-color: rgb(219.3829383886, 217.5616113744, 249.7383886256);
  border-color: #4f46e5;
}

.note-success {
  background-color: rgb(206.8048648649, 248.4951351351, 222.18);
  border-color: #16a34a;
}

.note-danger {
  background-color: rgb(245.5, 196.1, 196.1);
  border-color: #dc2626;
}

.note-warning {
  background-color: rgb(252.5707317073, 223.0146341463, 207.6292682927);
  border-color: #ea580c;
}

.note-info {
  background-color: rgb(225.3, 245.55, 252.3);
  border-color: #39c0ed;
}

.note-light {
  background-color: #fdfcff;
  border-color: #171717;
}

@media (min-width: 1199px) {
  .w-responsive {
    width: 75%;
  }
}

.bg-primary {
  background-color: rgba(37, 99, 235, var(--mdb-bg-opacity)) !important;
}

.bg-secondary {
  background-color: rgba(79, 70, 229, var(--mdb-bg-opacity)) !important;
}

.bg-success {
  background-color: rgba(22, 163, 74, var(--mdb-bg-opacity)) !important;
}

.bg-info {
  background-color: rgba(57, 192, 237, var(--mdb-bg-opacity)) !important;
}

.bg-warning {
  background-color: rgba(234, 88, 12, var(--mdb-bg-opacity)) !important;
}

.bg-danger {
  background-color: rgba(220, 38, 38, var(--mdb-bg-opacity)) !important;
}

.bg-light {
  background-color: rgba(253, 252, 255, var(--mdb-bg-opacity)) !important;
}

.bg-dark {
  background-color: rgba(23, 23, 23, var(--mdb-bg-opacity)) !important;
}

.bg-white {
  background-color: rgba(255, 255, 255, var(--mdb-bg-opacity)) !important;
}

.bg-black {
  background-color: rgba(0, 0, 0, var(--mdb-bg-opacity)) !important;
}

.bg-lighten {
